import React, { useEffect, useState } from "react";
import "./music.css";

const MusicPlayer = ({ track, url }) => {
  const [trackUrl, setTrackUrl] = useState(url);
  useEffect(() => {
    setTrackUrl(url);
  }, [url, track]);

  return (
    <div id="container">
      <img src={track.albumUrl} width="100" height="100" alt="" id="img" />
      <h1 className="title">{track ? track.title : "Song Name"}</h1>

      <audio
        controls
        style={{ width: "95%", marginLeft: "-4rem" }}
        autoPlay
        src={trackUrl}
      ></audio>
    </div>
  );
};

export default MusicPlayer;
