import { Button } from "@material-ui/core";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { handleDownload, handleUpload, playSong } from "../helper/apiCalls";
import MusicPlayer from "./MusicPlayer";

const backend = process.env.REACT_APP_BACKEND;

const Player = ({ track, speed }) => {
  const [url, setUrl] = useState("");
  const [names, setNames] = useState([]);

  const startUp = () => {
    axios
      .get(`${backend}/list`, {
        params: {
          internetSpeed: speed,
        },
      })
      .then((res) => {
        let data = JSON.parse(res.data);
        data.ListBucketResult.Contents.forEach((name) => {
          setNames((arr) => [...arr, name.Key._text]);
        });
      });
  };

  let data;
  const handlePlay = () => {
    data = playSong(speed, names, track);
    data.then((res) => setUrl(res));
  };

  const download = () => {
    let btn = document.getElementById("btn");
    btn.innerHTML = "Downloading...";
    let data = handleDownload(track, speed);
    data.then((res) => {
      btn.innerHTML = "Uploading...";
      console.log(res);
      let upload = handleUpload(res.name, res.dest, speed);
      upload.then((result) => {
        btn.innerHTML = "Upload Successful";
        console.log(result.data);
      });
    });
  };

  useEffect(() => {
    startUp();
    handlePlay();
  }, []);

  useEffect(() => {
    startUp();
    handlePlay();
  }, [track]);

  return (
    <div>
      {url ? (
        <div
          style={{
            width: "100%",
            position: "fixed",
            bottom: 0,
            background:
              "linear-gradient(90deg, rgba(157,89,203,1) 0%, rgba(255,104,104,1) 50%, rgba(233,180,105,1) 100%)",
            padding: "1rem 2rem",
            textAlign: "center",
          }}
        >
          <MusicPlayer track={track} url={url} />
        </div>
      ) : (
        <div
          style={{
            width: "100%",
            position: "fixed",
            bottom: 0,
            paddingBottom: "1rem",
            textAlign: "center",
            background:
              "linear-gradient(90deg, rgba(157,89,203,1) 0%, rgba(255,104,104,1) 50%, rgba(233,180,105,1) 100%)",
          }}
        >
          <h2>Song Not found in Library</h2>
          <Button
            color="primary"
            id="btn"
            variant="contained"
            onClick={download}
          >
            Download
          </Button>
        </div>
      )}
    </div>
  );
};

export default Player;
