import React, { useEffect, useState } from "react";
import useAuth from "./useAuth";
import SpotifyWebApi from "spotify-web-api-node";

import axios from "axios";

import Appbar from "./Components/Appbar";
import Home from "./Components/Home";
import Player from "./Components/Player";
import getSongs from "./helper/spotifyAPICalls";
import Loading from "./Components/Loading";

const backend = process.env.REACT_APP_BACKEND;
const spotifyApi = new SpotifyWebApi({
  clientId: process.env.REACT_APP_CLIENT_ID,
});

const Dashboard = ({ code, speed }) => {
  const accessToken = useAuth(code);

  const [search, setSearch] = useState();
  const [list, setList] = useState([]);
  const [searchResult, setSearchResult] = useState([]);
  const [playingTrack, setPlayingTrack] = useState();

  // const [lyrics, setLyrics] = useState("");

  const searchSongs = () => {
    let cancel = false;

    getSongs(accessToken, search, setSearchResult);
    return () => (cancel = true);
  };

  const chooseTrack = (track) => {
    setSearch("");
    setPlayingTrack(track);
    setSearchResult([]);
  };

  // const playingLyrics = () => {
  //   if (!playingTrack) return;

  //   axios
  //     .get(`${backend}/lyrics`, {
  //       params: {
  //         track: playingTrack.title,
  //         artist: playingTrack.artist[0].name,
  //       },
  //     })
  //     .then((res) => {
  //       setLyrics(res.data.lyrics);
  //     });
  // };

  const getList = () => {
    console.log("Your Internet SPEED is: ", speed);
    axios
      .get(`${backend}/list`, { params: { internetSpeed: speed } })
      .then((res) => JSON.parse(res.data))
      .then((data) => {
        data.ListBucketResult.Contents.forEach((song) => {
          setList((old) => [...old, song]);
        });
      });
  };

  useEffect(() => {
    if (speed) getList();
  }, [speed]);

  useEffect(() => {
    if (!accessToken) return;
    spotifyApi.setAccessToken(accessToken);
  }, [accessToken]);

  useEffect(() => {
    searchSongs();
  }, [accessToken, search]);

  // useEffect(() => {
  //   playingLyrics();
  // }, [playingTrack]);

  return (
    <div>
      <Appbar
        search={search}
        setSearch={setSearch}
        result={searchResult}
        setResult={setSearchResult}
        setPlayingTrack={setPlayingTrack}
        chooseTrack={chooseTrack}
      />
      <div style={{ marginTop: "5rem" }}></div>
      {list.length == 0 && (
        <div>
          <Loading />
        </div>
      )}
      {list.length !== 0 && (
        <div>
          <Home
            result={list}
            accessToken={accessToken}
            speed={speed}
            setPlayingTrack={setPlayingTrack}
          />
          <div style={{ marginBottom: "10rem" }}></div>

          {playingTrack && <Player track={playingTrack} speed={speed} />}
        </div>
      )}
    </div>
  );
};

export default Dashboard;
