import { useState } from "react";
import Dashboard from "./Dashboard";
import Login from "./Login";

import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";

const theme = createMuiTheme({
  palette: {
    primary: {
      light: "#339ba5",
      main: "#00838f",
      dark: "#005b64",
      contrastText: "#fff",
    },
    secondary: {
      light: "#df6843",
      main: "#d84315",
      dark: "#972e0e",
      contrastText: "#fff",
    },
  },
});

const code = new URLSearchParams(window.location.search).get("code");

function App() {
  const [speed, setSpeed] = useState(0);

  const getInternetSpeed = () => {
    var imageAddr =
      "https://upload.wikimedia.org/wikipedia/commons/b/b9/Pizigani_1367_Chart_1MB.jpg";
    var downloadSize = 1048576; //bytes
    var speedMbps;
    function InitiateSpeedDetection() {
      console.log("Checking your network speed, please wait...");
      window.setTimeout(MeasureConnectionSpeed, 1);
    }

    if (window.addEventListener) {
      window.addEventListener("load", InitiateSpeedDetection, false);
    } else if (window.attachEvent) {
      window.attachEvent("onload", InitiateSpeedDetection);
    }

    function MeasureConnectionSpeed() {
      var startTime, endTime;
      var download = new Image();
      download.onload = function () {
        endTime = new Date().getTime();
        showResults();
      };

      download.onerror = function (err, msg) {
        console.error("Invalid image, or error downloading");
      };

      startTime = new Date().getTime();
      var cacheBuster = "?nnn=" + startTime;
      download.src = imageAddr + cacheBuster;

      function showResults() {
        var duration = (endTime - startTime) / 1000;
        var bitsLoaded = downloadSize * 8;
        var speedBps = (bitsLoaded / duration).toFixed(2);
        var speedKBps = (speedBps / 1024).toFixed(2);
        var speedMBps = (speedKBps / 1024).toFixed(2);
        speedMbps = (speedMBps * 8).toFixed(2);
        setSpeed(speedMBps);
      }
    }
  };

  return (
    <ThemeProvider theme={theme}>
      {getInternetSpeed()}
      {code ? <Dashboard code={code} speed={speed} /> : <Login />}
    </ThemeProvider>
  );
}

export default App;
