import axios from "axios";

const backend = process.env.REACT_APP_BACKEND;
const MAX_SPEED = parseInt(process.env.REACT_APP_MAXSPEED);

export const playSong = async (speed, names, track) => {
  let bucket = "testmusicdata";
  if (speed > MAX_SPEED) {
    bucket = "testmusicdata";
  } else {
    bucket = "testmusicdata-lowrate";
  }
  let test;
  names.forEach((name) => {
    if (name.includes(track.title)) {
      test = name.replaceAll(" ", "%20");
    }
  });
  if (test)
    return `https://${bucket}.s3.us-south.cloud-object-storage.appdomain.cloud/${test}`;
};

export const handleDownload = async (track, speed) => {
  let data = await axios
    .get(`${backend}/download`, {
      params: {
        uri: track.externalUrl,
        title: track.title,
        artist: track.artist[0].name,
      },
    })
    .then((res) => {
      console.log("DONE");
      return {
        name: res.data.name,
        dest: res.data.dest,
      };
    });
  return {
    name: data.name,
    dest: data.dest,
  };
};

export const handleUpload = async (name, dest, speed) => {
  let data = await axios
    .get(`${backend}/upload`, {
      params: {
        name: name,
        dest: dest,
        internetSpeed: speed,
      },
    })
    .then((res) => {
      return res.data;
    });
  return {
    data,
  };
};
