import { makeStyles, useTheme } from "@material-ui/core/styles";

import SpotifyWebApi from "spotify-web-api-node";

import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import SkipPreviousIcon from "@material-ui/icons/SkipPrevious";
import PlayArrowIcon from "@material-ui/icons/PlayArrow";
import SkipNextIcon from "@material-ui/icons/SkipNext";
import Box from "@material-ui/core/Box";
import { useEffect, useState } from "react";
import getSongs from "../helper/spotifyAPICalls";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    background: "#cae6e8",
  },
  details: {
    display: "flex",
    flexDirection: "column",
  },
  content: {
    flex: "1 0 auto",
  },
  cover: {
    width: "10rem",
    height: "inherit",
  },
  controls: {
    display: "flex",
    alignItems: "center",
    paddingLeft: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  playIcon: {
    height: 38,
    width: 38,
  },
}));

const spotifyApi = new SpotifyWebApi({
  clientId: process.env.REACT_APP_CLIENT_ID,
});

const MAX_SPEED = parseInt(process.env.REACT_APP_MAXSPEED);

const CardComponent = ({
  index,
  accessToken,
  track,
  speed,
  setPlayingTrack,
}) => {
  const classes = useStyles();
  const theme = useTheme();
  const [result, setResult] = useState([
    {
      artist: [{ name: "Artist Name" }],
      title: "Song name",
      albumUrl:
        "https://www.thefennvoice.org/.a/6a017d3e8f0065970c022ad3c481a6200d-pi",
    },
  ]);

  const getSongInfo = (track) => {
    let search = track.Key._text;
    if (speed < MAX_SPEED) {
      search = search.substr(0, search.length - 8);
    } else {
      search = search.substr(0, search.length - 4);
    }

    getSongs(accessToken, search, setResult, true);
  };

  const handlePlay = (arg) => {
    setPlayingTrack(arg);
  };

  useEffect(() => {
    if (!accessToken) return;
    spotifyApi.setAccessToken(accessToken);
    getSongInfo(track);
  }, [accessToken]);

  return (
    <div style={{ margin: "1rem auto", width: "90%" }}>
      <Card className={classes.root}>
        <CardMedia
          className={classes.cover}
          image={result[0] ? result[0].albumUrl : "Image"}
          title="Live from space album cover"
        />
        <div className={classes.details}>
          <CardContent className={classes.content}>
            <Typography
              component="div"
              variant="h6"
              style={{
                width: "200px",
                whiteSpace: "nowrap",
              }}
            >
              <Box component="div" textOverflow="ellipsis" overflow="hidden">
                <strong>{result[0] ? result[0].title : "SONG NAME"}</strong>
              </Box>
            </Typography>
            <Typography variant="subtitle2" color="textSecondary">
              {result[0] ? result[0].artist[0].name : "Artist"}
            </Typography>
          </CardContent>
          <div className={classes.controls}>
            <IconButton aria-label="previous">
              <SkipPreviousIcon />
            </IconButton>
            <a onClick={() => handlePlay(result[0])}>
              <IconButton aria-label="play/pause">
                <PlayArrowIcon className={classes.playIcon} />
              </IconButton>
            </a>
            <IconButton aria-label="next">
              <SkipNextIcon />
            </IconButton>
          </div>
        </div>
      </Card>
    </div>
  );
};

export default CardComponent;
